import { _, statelessComponent, withNextFrame, render, Events, onClick, TrackByKey, key } from "ivi";
import { td, tr, table, tbody, div, li, ul, pre } from "ivi-html";
const TableCell = statelessComponent((t) => (Events(onClick(() => { console.log("Click", t); }), td("TableCell", _, t))));
const TableRow = statelessComponent(({ id, active, props }) => (tr(active ? "TableRow active" : "TableRow", { "data-id": id }, [
    TableCell("#" + id),
    props.map((item) => TableCell(item)),
])));
const Table = statelessComponent((p) => (table("Table", _, tbody(_, _, TrackByKey(p.items.map((item) => key(item.id, TableRow(item))))))));
const AnimBox = statelessComponent(({ id, time }) => (div("AnimBox", {
    "data-id": id,
    style: {
        "background": "rgba(0,0,0," + (0.5 + ((time % 10) / 10)) + ")",
        "border-radius": (time % 10) + "px",
    },
})));
const Anim = statelessComponent((p) => (div("Anim", _, TrackByKey(p.items.map((item) => key(item.id, AnimBox(item)))))));
const TreeLeaf = statelessComponent((p) => li("TreeLeaf", _, p.id));
const TreeNode = statelessComponent((p) => (ul("TreeNode", _, TrackByKey(p.children.map((n) => key(n.id, n.container ? TreeNode(n) : TreeLeaf(n)))))));
const Tree = statelessComponent((p) => div("Tree", _, TreeNode(p.root)));
function route(state) {
    switch (state.location) {
        case "table": return Table(state.table);
        case "anim": return Anim(state.anim);
        default: return Tree(state.tree);
    }
}
const Main = statelessComponent((state) => div("Main", _, state ? route(state) : null));
uibench.init("ivi", "1.0.0");
document.addEventListener("DOMContentLoaded", () => {
    const container = document.querySelector("#App");
    render(Main(), container);
    uibench.run((state) => withNextFrame(() => { render(Main(state), container); })(), (samples) => { render(pre(_, _, JSON.stringify(samples, undefined, 2)), container); });
});
